import * as React from 'react';
import { render } from '@testing-library/react';
import Zhi from './reader';
import imTag from './fixtures/imTag.json';

describe('Zhi:imTag', () => {
  it('正确渲染', () => {
    const asl = [
      'root',
      [
        'p',
        [
          'span',
          {
            'data-type': 'text',
          },
          [
            'span',
            {
              'data-type': 'leaf',
            },
            'This is paragraph.',
          ],
        ],
        imTag,
      ],
    ];
    const container = render(<Zhi content={asl} />);
    expect(!!container.getByTestId('im')).toBe(true);
  });
});